@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/sandstone/bootstrap.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.13.0/css/all.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#map {
  height: 170px;
  width: 100%
}

input {
  transition: all 0.40s ease-in-out !important;
  outline: none !important
}

.form-control:focus {
  box-shadow: 0 0 2px hsl(40, 97%, 54%) !important;
  border-color: #fcaf17 !important
}

.confirm:focus, .confirm.focus {
  background-color: #d23430;;
}

.minus:hover {
  color: #d23430
}
.historic-city:hover   {
  background-color: #F8F5F0;
  cursor: pointer
}

.historic-city {
  animation: fadein 1.75s 1;
  -webkit-animation: fadein 1.75s 1; /* Safari 4.0 - 8.0 */
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;  /*kills it on the last frame*/
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  position: static;
}

/* Standard Browsers */
@keyframes fadein {
    0% { opacity: 0;}
    50% {opacity: .35;}
    75% {opacity: .65;}
    100% {opacity: 1;}
  }
/*  Safari */
@-webkit-keyframes fadein {
    0% { opacity: 0;}
    50% {opacity: .35;}
    75% {opacity: .65;}
    100% {opacity: 1;}
}
/* IE and Edge */
